.image-button {
  z-index: 2;
  width: 50vh;
  height: 13vh;
}

.image-button:hover {
  scale: 110%;
}

.image-button:active {
  width: 50vh;
  height: 13vh;
}

#episodes-button:hover {
    cursor: pointer;
    content: url('https://yondergarden.s3.us-east-2.amazonaws.com/defaultassets/_0000_episodesButtonHover.png');
  }

#premium-button:hover {
  cursor: pointer;
  content: url('https://yondergarden.s3.us-east-2.amazonaws.com/defaultassets/_0002_premiumButtonHover.png');
}

#subscribe-button:hover {
  cursor: pointer;
  content: url('https://yondergarden.s3.us-east-2.amazonaws.com/defaultassets/_0001_subscribeButtonHover.png');
  z-index: 100;
}

#about-button:hover {
  cursor: pointer;
  content: url('https://yondergarden.s3.us-east-2.amazonaws.com/defaultassets/_0003_aboutButtonHover.png');
}

.spin-hover:hover {
  transform: rotate(7deg);
}

.center-home {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
  margin-left: auto;
  margin-right: auto;
  width: 100vh;
  height: 100vh;
}

.center-home-premium {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
  margin-left: auto;
  margin-right: auto;
  width: 100vh;
  height: 100vh;
  filter: hue-rotate(0deg);
  animation: hueRotate 3s linear infinite;
}

@keyframes hueRotate {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

/*======================
    About Pop-up
========================*/

.info-box {
  border: .5vh solid #000000;
  background-color: pink;
  z-index: 49;
  min-width: 54vh;
  max-width: 54vh;
  min-height: 54vh;
  max-height: 54vh;
  overflow: hidden
}

.about-info {
  width: 100%;
  height: 100%;
  text-align: center;
  object-fit: cover; /* Adjusts the size of the image to cover the div, possibly cropping it */
}

h1 {
    font-size: 6vh;
    margin: 2vh 0;
}

h2 {
    font-size: 1.2em;
    margin: 2vh;
}

.boxes {
    display: flex;
    justify-content: space-around;
}

.rectangle {
    background-color: #ffffe0; /* Light yellow */
    margin: 1vh;
    padding: 2vh;
    border: 0.3vh solid black; /* Black border */
    border-radius: 2vh;
    box-shadow: 0 0 1vh yellow; /* Yellow glow */
    flex: 1;
    max-width: 45%;
}

.rectangle h3 {
    margin: 0 0 1vh;
    font-size: 1.5em;
}

.rectangle p {
    margin: 0;
    font-size: 1em;
}

.progress-container {
    display: flex;
    align-items: center;
    margin-top: 2vh;
    margin-left: 1vh;
    margin-right: 1vh;
}

.progress-bar {
    width: 50vh;
    height: 2vh;
    background-color: #ddd;
    border: 0.3vh solid black; /* Black border */
    border-radius: 10vh;
    overflow: hidden;
    box-shadow: 0 0 1vh blue;
    position: relative;
}

.progress {
    height: 100%;
    background-color: yellow;
}

.progress-text {
  margin-right: 1vh;
}

/*======================
    Subscribe Pop-up and Icons
========================*/

.subscribeHeader {
  margin-top: 10vh;
}

/*======================
    Episodes Pop-up and Icons
========================*/

.episodes-box {
  border: .5vh solid #000000;
  background-color: pink;
  z-index: 49;
  min-width: 54vh;
  max-width: 54vh;
  min-height: 54vh;
  max-height: 54vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.episodes-icon-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1vh;
  margin: 3vh 3vh 3vh 6vh;
}

.episodes-icon-wrapper {
  position: relative;
}

.episode-tooltip {
  position: absolute;
  bottom: 90%; /* Position above the image */
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  background-color: yellow;
  color: red;
  border: 0.2vh solid black;
  padding: 5px;
  text-align: center;
  display: inline-block;
  width: auto;
  white-space: nowrap;
  z-index: 1000;
}

.episodes-icon {
  object-fit: cover;
  border: 0.3vh solid black;
  display: inline-block;
  background-repeat: no-repeat;
  font-size: 50px;
  max-width: 9vh;
  position: relative;
}

.episodes-icon:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.episodes-icon:active {
  cursor: pointer;
  transform: rotate(3.5deg) scale(1);
}

/*======================
        Premium
========================*/

.premium-box {
  filter: hue-rotate(0deg);
  animation: hueRotate 3s linear infinite;
}



/*======================
      Close Button
====================*/


.closeButton {
  position: fixed;
  top: 5vh;
  left: 5vh;
  background: #FF6666; /* Handle color */
  border: 'solid black';
  color: #5D2E8C; /* Track color */
  padding: 0.5vh;
  width: auto;
  height: auto;
  text-align: center;
  z-index: 10000;
}

.closeButton:hover {
  cursor: pointer;
  background: #FFFF00; /* Handle color on hover */
  transform: scale(1.1)
}

.closeButton:active {
  cursor: pointer;
  background: #CCFF66; /* Handle color on hover */
  transform: scale(1)
}

/*======================
      Scrollbar
========================*/

::-webkit-scrollbar-track {
  background: #5D2E8C; /* Track color */
  box-shadow: inset 0 0 5px black;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: #FF6666; /* Handle color */
  box-shadow: inset 0 0 5px red;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #FFFF00; /* Handle color on hover */
}

::-webkit-scrollbar {
  width: 1.5vh; /* Width of the scrollbar */
}

